export const schemaString = `
type Query {
    users: [User]!
    auth(userName: String,userPassword: String): AuthResult
    course:[Course]!
    teacher:[Teacher]!
    student:[Student]!
}

type AuthResult{
    user: User
    token: String
}
type User {
    _id: ID!
    userName: String
    userEmail: String
    userPassword: String
    userRole: String
}

type Course {
    _id: ID!
    courseId: String!
    courseName: String
    information: String
    students(gender: String): [Student]
    teachers:Teacher
}

type Teacher {
    _id: ID!
    courseId: String!
    name: String!
    course: [Course]
}

type Student {
    _id: ID!
    studentName: String!
    gender: String!
    phone: String!
    courses: [Course]
}
`