import { Collection, MongoClient } from "mongodb";
import { queryUserValue } from "src/graphQlFiles/graphQLfn";
import { Table } from "../types/Table";

const dbConnectionUrl =
  "mongodb+srv://leilu0229:AAaa123581321@cluster0.vm8df.mongodb.net/ServerlessCourse?retryWrites=true&w=majority";

//1. initial function return promise
//2. map

const dbObject = async (dbCollectionName: string): Promise<Collection> => {
  const dbInstance = await MongoClient.connect(dbConnectionUrl);
  const dbObject = dbInstance.db("ServerlessCourse");
  const dbCollection = dbObject.collection(dbCollectionName);
  return dbCollection;
};

const dbData = async <T>(tableName: string): Promise<T[]> => {
  const result = await dbObject(tableName);
  return result.find().toArray();
};

const dbAuth = async <T>(data: queryUserValue): Promise<T | null> => {
  const result = await dbObject(Table.User);
  return result.findOne({
    userEmail: data.userName,
    userPassword: data.userPassword,
  });
};

const dbQuery = async <T>(
  tableName: string,
  searchKey: object
): Promise<T[]> => {
  const result = await dbObject(tableName);
  return result.find(searchKey).toArray();
};

const dbAggregate = async <T>(
    tableName: string,
    localValue: string,
    lookupTable: string,
    lookupAttribute: number | string,
    asValue: string
  ) => {
    const collection = await dbObject(tableName);
    return collection
      .aggregate([
        {
          $lookup: {
            from: lookupTable,
            localField: localValue,
            foreignField: lookupAttribute,
            as: asValue,
          },
        },
      ])
      .toArray();
  };

export { dbData, dbAuth, dbQuery, dbAggregate };
