import { User } from "src/types/User";
import { dbServiceGet } from "../service/MongoDbService";
import { Table } from "../types/Table";
import { dbAuth } from "../service/MongoDBGraphQL";
import { jwtSign } from "../jwt/jwt-tools";

type authValue = {
  userName: string;
  userPassword: string;
};
export default {
  Query: {
    users: () => dbServiceGet(Table.User, {}, 0),
    auth: async (parent: null, { userName, userPassword }: authValue) => {
      const result = await dbAuth<User>({ userName, userPassword });
      if (result) {
        const token = jwtSign({ userName, userPassword });
        return {
          user: result,
          token: token,
        };
      }
      return result;
    },
  },
};
