import { Table } from "../types/Table";
import DataLoader from "dataloader";
import { Course, Student, Teacher } from "src/types";
import { dbAggregate, dbData, dbQuery } from "../service/MongoDBGraphQL";

type studentArg = {
  gender: string;
};

//dataloader

const teacherFn = async (courseIds: Readonly<string[]>) => {
  const DBTeachers = await dbData<Teacher>(Table.Teacher);
  const classes = DBTeachers.filter((teacher) =>
    courseIds.includes(teacher.courseId)
  );
  return classes;
};
const teacherLoader = new DataLoader(teacherFn);

//query
//
export default {
  Query: {
    course: (context: any) =>dbAggregate<Course>(Table.Course, "_id", "Class", "courseId", "students")
  },
  Course: {
    teachers: (parent: Course) => teacherLoader.load(parent._id),
    students: (parent: Course, arg: studentArg) => getStudents(parent, arg),
  },
};

//help function
const getStudents = async (parent: Course, arg: studentArg) => {
  const allStudent: Student[] = [];
  if (parent.students) {
    for (const s of parent.students) {
      const student = await dbQuery<Student>(Table.Student, {
        studentName: s?.studentName,
      });
      if (student[0].gender == arg.gender) {
        allStudent.push(student[0]);
      }
    }
  }
  return allStudent;
};
