import { Table } from "../types/Table";
import { Course, Student } from "src/types";
import { dbAggregate, dbQuery } from "../service/MongoDBGraphQL";

export default {
  Query: {
    student: () =>
      dbAggregate<Student>(
        Table.Student,
        "_id",
        "Class",
        "studentId",
        "courses"
      ),
  },
  Student: {
    courses: (parent: Student) => getCourses(parent),
  },
};

//help function
const getCourses = async (parent: Student) => {
  const allCourse: Course[] = [];
  if (parent.courses) {
    for (const c of parent.courses) {
      const course = await dbQuery<Course>(Table.Course, {
        _id: c?.courseId,
      });
      //console.log(course)
      allCourse.push(course[0]);
    }
  }
  return allCourse;
};
